<template>
<div v-if="ready" class="col-7">
    <form enctype="multipart/form-data" @submit.prevent="handleSubmit()" >
        <div class="card">
            <div class="card-header">
                <strong>Medical Allowance Setting</strong>
            </div>            
            <div class="card-body card-block">
                <div class="col-12 flex" style='margin-bottom:1rem'>             
                    <div class="col-6"></div>
                    <div class="col-3">
                        <label>Start Period</label>
                        <input type="month" readonly v-model="st" class="form-control"/>
                    </div>
                    <div class="col-3">
                        <label>End Period</label>
                        <input type="month" readonly v-model="ed" class="form-control"/>
                    </div>
                </div>
                    <div class="col-12 flex" style='margin-top:1rem;margin-bottom:1rem'>
                        <div class="col-6">
                            Teachers
                        </div>
                        <div class="col-6">
                            <input type="number" onfocus="this.value=''" step="0.01" class="form-control" v-model="teacher_val"/>
                        </div>
                    </div>
                <div class="col-12 flex" style='margin-top:1rem;margin-bottom:1rem'>
                    <div class="col-6">
                        Non-Teaching Staff
                    </div>
                    <div class="col-6">
                        <input type="number" onfocus="this.value=''" step="0.01" class="form-control" v-model="nts_val"/>
                    </div>
                </div>
            </div>
            <div class="card-footer">
                <button type="submit" class="btn btn-primary btn-sm">
                    <i class="fa fa-paper-plane"></i> Save
                </button>
                <button type="reset" class="btn btn-danger btn-sm">
                    <i class="fa fa-ban"></i> Reset
                </button>
            </div>
        </div>
    </form>
</div>
</template>
<script>
export default {
    data(){
        return{
            med_list:[],
            teacher_val:0.0,
            nts_val:0.0,
            ready:false,
            period:'',
            st:'',
            ed:'',
            user_check:'',
        }
    },
    async created(){
        await axios.get('Procurement/shared_backend/check_user_setup.php')
        .then(
            (response)=>{
                this.user_check = response.data[0].signed;
                var perm = response.data[0];
                if(!(this.user_check=='set')){
                    this.$router.push('../user_setup');
                }
                if(perm.proc_level<4){
                    this.$alert("No permission").then(
                        ()=>{
                            this.$router.go(-1);
                        }
                    )
                }
            }
        )
        await axios.get('Procurement/admin/backend/med_period.php')
            .then(
                (response)=>{
                    this.st = response.data.st;
                    this.ed = response.data.ed;
                }
            )
        var formData = new FormData();
        formData.append('st',this.st);
        formData.append('ed',this.ed);
        await axios.post('Procurement/admin/backend/fetch_med_setup.php',formData)
            .then(
                (response)=>{
                    this.med_list = response.data;
                    if(!(response.data=='Empty')){
                        var i = 0;
                        for(i=0;i<this.med_list.length;i++){
                            if(this.med_list[i].type=='Teachers'){
                                this.teacher_val = this.med_list[i].max;
                            } else if(this.med_list[i].type=='Non-Teaching-Staff'){
                                this.nts_val = this.med_list[i].max;
                            }
                        }                        
                    }
                }
            )
    this.ready = true;
    },
    methods:{
        handleSubmit(){
            var formData = new FormData();
            formData.append('st',this.st);
            formData.append('ed',this.ed);
            formData.append('teacher_val',this.teacher_val);
            formData.append('nts_val',this.nts_val);
            axios.post('Procurement/admin/backend/update_med_val.php',formData)
                .then(
                    (response)=>{
                        if(response.data=='OK'){
                            this.$alert('Successfully Updated');
                        } else {
                            this.$alert(response.data);
                        }
                    }
                )
        }
    }
}
</script>


